import React from 'react';
import  { Card, CardMedia, CardContent, CardActions, Typography, IconButton } from '@material-ui/core';
import { AddShoppingCart } from '@material-ui/icons';
import useStyles from './styles'


const Produit = ({ produit , handleAddToCart}) => {
    const classes = useStyles();
    
   
    return (
        <Card className={classes.root}>
            <CardMedia className={classes.media} image={ produit.media.source } title={ produit.name } />
            <CardContent>
                <div className="classes cardContent">
                  <Typography variant="h5" gutterBottom>
                      {produit.name}
                  </Typography>
                  <Typography variant="h5" >
                      {produit.price.formatted_with_symbol}
                  </Typography>
                </div>
                <Typography dangerouslySetInnerHTML= {{ __html: produit.description}} variant="body2" color="textSecondary" />
            </CardContent>
            <CardActions disableSpacing className="cardActions">
                <IconButton aria-label="Ajoutter au panier" onClick={() => handleAddToCart(produit.id,1)} >
                    <AddShoppingCart />
                </IconButton>
            </CardActions>
        </Card>
    )
}

export default Produit;
